export function automobilesString(num) {
  if (num % 10 === 0) return num + " ";

  let n = num % 100;

  if (n < 10 || n > 20) {
    if (n % 10 === 1) return num + " ";
    else if (n % 10 < 5) return num + " ";
  }
  return num + " ";
}

export function oneDecimal(num) {
  return Number(num).toFixed(1);
}
